html,body{
  background-color: #222222;
  width: 100%;
  height: 100%;
}
#root{
  width: 100%;
  height: 100%;
}
.stage{
  position: relative;
  width: 100%;
  height: 100%;
}
.image-section{
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: #ddd;
  -webkit-perspective: 800px;
          perspective: 800px;
}
.controller-nav{
  position: absolute;
  bottom: 30px;
  z-index: 1;
  width: 100%;
  text-align: center;
  font-size: 0;
}

.controller-nav .controller-unit{
  width: 30px;
  height: 30px;
  margin: 0 5px;
  display: inline-block;
  font-size: 14px;
  text-align: center;
  cursor: pointer;
  background-color: #aaa;
  border-radius: 50%;
  -webkit-transform: scale(0.5);
      -ms-transform: scale(0.5);
          transform: scale(0.5);
  vertical-align: middle;
}

@font-face {
  font-family: "icon-turn-arrow";
  src: url("./fonts/icons/turn-arrow.woff") format("woff"),
  url("./fonts/icons/turn-arrow.svg") format("svg"),
  url("./fonts/icons/turn-arrow.eot") format("embedded-opentype"),
  url("./fonts/icons/turn-arrow.ttf") format("truetype");
}

.controller-nav .controller-unit.is-center{
  background-color: #888;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
}
.controller-nav .controller-unit.is-center::after{
  content: "\e600";
  font-family: "icon-turn-arrow";
  color: #fff;
  line-height: 30px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.controller-nav .controller-unit.is-inverse{
  background-color: #555;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}


