.image-figure-container{
  width: 240px;
  height: 300px;
  position: absolute;
  -webkit-perspective: 800px;
          perspective: 800px;
  -webkit-transform-origin: 0 50% 0;
      -ms-transform-origin: 0 50% 0;
          transform-origin: 0 50% 0;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: 0.6s;
  -o-transition: 0.6s;
  transition: 0.6s;
}

/* 正面 */
.image-figure{
  position: absolute;
  background-color: #fff;
  width: 100%;
  height: 100%;
  margin: 0;
  z-index: 1;
  text-align: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 20px;
  overflow: hidden;
  /* transform-origin: 0 50% 0; */
  /* transition: all 2s; */
}

.image-figure img{
  width: 200px;
  height: 200px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.image-title{
  margin: 0;
  line-height: 60px;
  color: #a7a0a2;
  font-size: 16px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

/* 反面 */
.image-desc{
  position: absolute;
  background-color: #fff;
  width: 100%;
  height: 100%;
  text-align: center;
  color: #666;
  font-size: 16px;
  line-height: 1.5;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 20px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  /* transform-origin: 0 50% 0; */
  /* transform: translate(240px) rotateY(-180deg); */
  -webkit-transform: rotateY(-180deg);
          transform: rotateY(-180deg);
  -webkit-transition: 0.6s;
  -o-transition: 0.6s;
  transition: 0.6s;
}

/* 翻转 */
.inverse{
  /* transform-style: preserve-3d; */
  -webkit-transform: translate(240px) rotateY(180deg);
          transform: translate(240px) rotateY(180deg);
  -webkit-transition: 0.6s;
  -o-transition: 0.6s;
  transition: 0.6s;
}
.inverse .image-figure{
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  /* transform: translate(240px) rotateY(180deg);
  transition: all 2s; */
}
.inverse .image-desc{
  /* transform: rotateY(-180deg); */
  /* transition: all 2s; */
}


